<template>
    <td :class="{'text-xs-center':column.align == 'center', 'text-xs-right':column.align =='right'}">
        <component v-if="column.renderer" :data="rawValue" :is="column.renderer" :row="row" :column="column" />
        <slot v-else :value="value" :row="row" :column="column">
            {{value}}
        </slot>
    </td>
</template>
<script>
/////////////////////////////////

export default {
    props: {
        'column': {
            type: Object,
        },
        'row': {
            type: Object,
        },
    },
    computed: {
        key() {
            return this.column.key;
        },
        rawValue() {
            return _.get(this.row, this.key);
        },
        value() {
            var val = this.rawValue;

            ///////////////////////////////

            //If there is an array
            if (_.isArray(val)) {
                return _.chain(val)
                    .compact()
                    .map(function(object) {
                        return object.title || object.name;
                    })
                    .compact()
                    .value()
                    .slice(0, 10)
                    .join(', ');
            }

            ///////////////////////////////

            if (_.isObject(val)) {

                if (val._type == 'event') {
                    return val.title + '<span class="text-muted"> // ' + this.$fluro.formatDate(val.startDate, 'h:mma dddd D MMM, Y') + '</span>';
                }

                return val.title || val.name;
            }

            return val;


        },
    }
}
</script>
